.csv-table-wrapper {
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
}

.csv-table {
  border-collapse: collapse;
  &__header {
    background-color: #000;
    th {
      font-size: 18px;
      text-align: left;
      padding: 10px 20px;
      color: #fff;
    }
  }
  &__body {
    background-color: #dbdbdb;
    td {
      height: 20px;
      padding: 10px 20px;
    }
    tr + tr {
      border-top: 2px #00f solid;
    }
    tr:nth-child(odd) {
      background-color: #fff;
    }
  }
}
